.comment_form {
    width: 500px;
    margin: 0 auto;
    border: 3px solid white;
    border-radius: 15px;
    padding: 30px 0;
    background-color: #addcfa;
    text-align: center;
}

.new_user_name,
.new_comment {
    display: block;
}

.new_user_name_content {
    margin-bottom: 15px;
}

.new_comment_content {
    margin-bottom: 15px;
}

input, textarea {
    margin: 10px auto;
    border: none;
    border-radius: 5px;
    display: block;
}
