.comment_item {
    margin: 20px auto;
    border: 1px dotted gray;
    border-radius: 5px;
    padding: 10px;
    background-color: aliceblue;
    display: grid;
    grid-template-columns: minmax(min-content, 4fr) 1fr;
}

