.user_name {
    font-weight: bold;
    margin-right: 20px;
    text-align: left;
}

.comment {
    font-style: italic;
    font-weight: normal;
}

.date {
    font-size: .7em;
    text-align: right;
    padding-top: 5px;
    font-weight: normal;
}

.delete_comment {
    align-self: center;
}

